.container {
  background-color: grey;
  width: 1000px;
  height: 1000px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 200px;
  grid-template-rows: repeat(3, 1fr) 200px;
  grid-gap: 30px;
  grid-template-areas: "head head head head " "box-1 box-2 box-3 side" "main main main side" "footer footer footer footer"; }

.item {
  padding: 20px;
  font-size: 30px;
  font-family: sans-serif;
  color: white;
  &-1 {
    background-color: red;
    grid-area: head; }


  &-2 {
    background-color: pink;
    grid-area: box-1; }


  &-3 {
    background-color: blue;
    grid-area: box-2; }
  &-4 {
    background-color: brown;
    grid-area: box-3; }

  &-5 {
    background-color: orange;
    grid-area: side; }


  &-6 {
    background-color: green;
    grid-area: main; }

  &-7 {
    background-color: yellowgreen;
    grid-area: footer; } }





